import { Container, Nav, Navbar, Image } from 'react-bootstrap'
import { useNavigate, useLocation, Link } from 'react-router-dom';
import Logo from 'assets/images/logo.gif'
import DiscordLogo from 'assets/images/Discord.gif'
import TwitterLogo from 'assets/images/Twitter.gif'
import './Header.scss'
import ConnectWalletButton from 'components/ConnectWalletButton';
import RoundButton from 'components/RoundButton';
import { ConnectButton } from "@rainbow-me/rainbowkit";

export default function Header() {
  const navigate = useNavigate();
  const { pathname, hash } = useLocation();
  if (hash === '#about') {
    window.location.href = '#about'
  }
  return (
    <Navbar expand="lg" bg="#1c1817" data-bs-theme="dark" variant="dark" sticky="top">
      <Container fluid>
        <Nav className='align-items-center d-flex flex-row'>
          <Nav.Item>
            <Navbar.Brand href='#' onClick={() => navigate('/')} className='m-0'>
              <Image src={Logo} width='55px' />
            </Navbar.Brand>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link className='p-0' href="" target='_blank'>
              <Image src={TwitterLogo} width='55px' />
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link className='p-0' href="" target='_blank'>
              <Image src={DiscordLogo} width='55px' />
            </Nav.Link>
          </Nav.Item>
        </Nav>
        <Navbar.Toggle aria-controls="navbar-nav" />
        <Navbar.Collapse id="navbar-nav" className='position-relative'>
          {/* <ConnectWalletButton /> */}
          <ConnectButton.Custom>
            {({
              account,
              chain,
              openAccountModal,
              openChainModal,
              openConnectModal,
              authenticationStatus,
              mounted,
            }) => {
              // Note: If your app doesn't use authentication, you
              // can remove all 'authenticationStatus' checks
              const ready = mounted && authenticationStatus !== 'loading';
              const connected =
                ready &&
                account &&
                chain &&
                (!authenticationStatus ||
                  authenticationStatus === 'authenticated');

              return (
                <div
                  {...(!ready && {
                    'aria-hidden': true,
                    'style': {
                      opacity: 0,
                      pointerEvents: 'none',
                      userSelect: 'none',
                    },
                  })}
                  className='button-group'
                >
                  {(() => {
                    if (!connected) {
                      return (
                        <ConnectWalletButton onClick={openConnectModal} type="button" className="connect-btn">
                          Connect Wallet
                        </ConnectWalletButton>
                      );
                    }

                    if (chain.unsupported) {
                      return (
                        <RoundButton onClick={openChainModal} type="button" variant='warning'>
                          Wrong network
                        </RoundButton>
                      );
                    }

                    return (
                      <div className='d-flex gap-3 isConnected'>
                        <RoundButton
                          onClick={openChainModal}
                          type="button"
                          className='d-flex align-items-center'
                          variant='primary'
                        >
                          {chain.hasIcon && (
                            <div
                              style={{
                                background: chain.iconBackground,
                                width: 12,
                                height: 12,
                                borderRadius: 999,
                                overflow: 'hidden',
                                marginRight: 4,
                              }}
                            >
                              {chain.iconUrl && (
                                <img
                                  alt={chain.name ?? 'Chain icon'}
                                  src={chain.iconUrl}
                                  style={{ width: 12, height: 12 }}
                                />
                              )}
                            </div>
                          )}
                          {chain.name}
                        </RoundButton>

                        <RoundButton onClick={openAccountModal} type="button" variant='success'>
                          {account.displayName}
                          {account.displayBalance
                            ? ` (${account.displayBalance})`
                            : ''}
                        </RoundButton>
                      </div>
                    );
                  })()}
                </div>
              );
            }}
          </ConnectButton.Custom>
          <Nav className="ms-auto align-items-center" activeKey={pathname}>
            {/* <Nav.Link href='/presale' to='/presale' as={Link}>PRESALE & CLAIM</Nav.Link> */}
            <Nav.Link href='/how_to_buy.pdf' to='/how_to_buy.pdf' target='_blank' as={Link}>HOW TO BUY</Nav.Link>
            <Nav.Link href='/#about' to='/#about' as={Link}>ABOUT</Nav.Link>
            <Nav.Link href='/earn' to='/earn' as={Link}>EARN</Nav.Link>
            <Nav.Link href='/earn' to='/betting' as={Link}>BETTING</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}
